
import { defineComponent } from 'vue';
import http from "@/views/ts/http";
import router from '../../router'

export default defineComponent({
    name: 'LoginPage',
    data() {
        return {
            token:null,
            nomailalert: '',
            nopasswordalert: '',
            mailorpasswrong: ''
        };
    },
    methods: {
        login() {
/** バリデーション **/
            const MAElement = this.$refs.mail_address as HTMLInputElement
            const PAElement = this.$refs.password as HTMLInputElement

            this.nomailalert = '';
            this.nopasswordalert = '';
            this.mailorpasswrong = '';

            var flag = true;

            if(MAElement.value == ''){
                this.nomailalert = "メールアドレスを入力してください。";
                flag = false;
            }
            if(PAElement.value == ''){
                this.nopasswordalert = "パスワードを入力してください。";
                flag = false;
            }
            if(flag !== true){
                return null;
            }
/** バリデーション **/
            return http.post(
            "/api/login",
            {
                "mail_address":MAElement.value,
                "password":PAElement.value
            })
            .then(response => {
                this.token = response.data.token;
                localStorage.setItem('access_token', response.data.token);
                //ダッシュボードへリダイレクト
                router.beforeEach((to, from, next) => {
                    next({ name: 'Dashboard' });
                });
            })
            .catch(error => {
                console.log(error);
                this.mailorpasswrong = 'メールアドレス、またはパスワードが違います。';
            });
        },

        hoge(): Promise<any> {
            return http.get(
            "/api/hoge",
            {
                headers: {
                    Authorization: `Bearer ${this.token}`
                }
            })
            .then(response => {
                console.log(localStorage.getItem('access_token'));
            })
            .catch(error => {
                console.log(error);
            });
        },

        logout(): Promise<any> {
            return http.post(
            "/api/logout"
            )
            .then(response => {
                console.log(response.data);
            })
            .catch(error => {
                console.log(error);
            });
        }
    },

});
